/* hamburger styles start*/
.overlay {
  display: none; }

.hamburger-icon-container {
  width: 30px;
  height: 22px;
  display: flex;
  justify-content: center;
  align-items: center;
  cursor: pointer; }

.hamburger-icon-container:hover {
  box-sizing: border-box;
  border: 2px dotted #011c40; }

.hamburger {
  visibility: hidden;
  width: 24px;
  height: 12px; }

.hamburger-icon {
  position: relative;
  width: 24px;
  height: 2px;
  background-color: #f9fbfd;
  cursor: pointer; }

.hamburger-icon::before {
  position: absolute;
  left: 0;
  top: -4px;
  margin-top: -2px;
  content: "";
  display: block;
  width: 24px;
  height: 2px;
  background-color: #f9fbfd; }

.hamburger-icon::after {
  position: absolute;
  left: 0;
  top: 4px;
  margin-top: 2px;
  content: "";
  width: 24px;
  height: 2px;
  background-color: #f9fbfd; }

.hamburger-icon-container:hover + .hamburger-icon:hover,
.hamburger-icon-container:hover + .hamburger-icon:hover::before,
.hamburger-icon-container:hover + .hamburger-icon:hover::after {
  background-color: #011c40;
  cursor: pointer; }

.hamburger-icon:hover,
.hamburger-icon:hover::before,
.hamburger-icon:hover::after {
  background-color: #011c40;
  cursor: pointer; }

.hamburger-icon:active,
.hamburger-icon:active::before,
.hamburger-icon:active::after {
  background-color: #011c40; }

